@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oleo+Script+Swash+Caps&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=PT+Serif:ital,wght@1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ebad88;
  background-image: url('./img/background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 24px;
}

/* ----- Info Text ----- */
.info-container {
  width: 550px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(80deg, transparent, rgba(255, 255, 255, .65), rgba(255, 255, 255, .85), rgba(255, 255, 255, .65), transparent);
  margin: .75em;
}

.info-text {
  color: #188ccf;
  font-family: 'PT Serif', serif;
  font-size: .9em;
  letter-spacing: .5px;
  text-align: center;
  padding: .65em;
}

/* ----- Quote Pad ----- */
.quote-pad {
  width: 550px;
  background-image: linear-gradient(#958574, #685b4d);
  border-radius: .5em;
  padding-bottom: 1em;
  overflow: hidden;
}

/* ----- Pad Header ----- */
.app-header {
  height: 3em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-image: linear-gradient(#474149, #352d36);
}

.app-title {
  color: white;
  font-family: 'Lobster', cursive;
  font-size: 1.65em;
  font-weight: 400;
  letter-spacing: 1px;
  margin-left: .5em;
}

/* ----- Add Button ----- */
.add-button {
  width: 4em;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #c53f53;
  color: white;
  font-family: 'Staatliches', cursive;
  font-size: 1em;
  font-weight: 400;
  letter-spacing: 1px;
  border: none;
  border: 5px solid transparent;
  border-radius: .2em;
  cursor: pointer;
  margin-right: .65em;
}

.icon {
  font-size: 1em;
}

.add-button:hover {
  background-color: #e64860;  
}

/* ----- Quote Form ----- */
.quote-form {
  width: 100%;
  height: 9.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: white;
  padding-bottom: .25em;
}

.form-group {
  width: 75%;
  display: flex;
  flex-direction: column;
  margin-bottom: .5em;
}

label {
  font-family: 'Roboto Slab', serif;
  font-size: .8em;
  margin-bottom: 1px;
}

input {
  background-color: #efe8d7;
  border: 1px solid #898376;
  padding: .4em;
  font-size: .65em;
  outline: none;
}

input:focus {
  border: 1px solid #403b42;  
}

input:focus::placeholder {
    color: transparent;
}

.write-button {
  width: 4.75em;
  height: 1.85em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 .35em 0 .35em;
  border: 2px solid transparent;
  border-radius: .2em;
  background-color: #b0bdc0;
  cursor: pointer;
  color: #2e2850;
  font-family: 'Oleo Script Swash Caps', cursive;
  font-size: 1em;
  letter-spacing: 1px;
  margin: .25em;
}

.write-icon {
  font-size: 1.15em;
}

.write-button:hover {
  background-color: #c4dde2;
  color: #4737a0;
  border: 2px solid #4737a0;  
}

/* ----- Quotes Container ----- */
.quotes-container {
  width: 92%;
  margin: auto;
  border: 1px solid rgba(0, 0, 0, .25);
  border-top: none;
  border-radius: 0 0 .5em .5em;
  overflow: hidden;
}

/* ----- Quote ----- */
.quote {
  background-color: #fff6d5;
  background-image: url('../src/img/paper-texture.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-family: 'Caveat', cursive;
  letter-spacing: 1px;
  padding: .45em .7em;
  border-top: 3px solid #87a7d3;
}

.quote-text {
  color: #3b419b;
  font-size: 1.4em;
  line-height: 1.2em;
}

.author {
  color: #b63b60;
  font-size: 1.2em;
  text-align: right;
  padding-top: .25em;
}

/* ----- No Quotes ----- */
.no-quotes {
  display: flex;
  justify-content: center;
  font-family: 'Poppins', sans-serif;
  font-size: .75em;
  color: white;
  margin-top: 1em;
}

/* ----- Footer Text ----- */
.footer-text {
  height: 4.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #6d3455;
  font-family: 'Rubik', sans-serif;
  font-size: .8em;
}

.footer-text a {
  color: inherit;
}

.footer-text a:hover {
  color: white;
}

/* ----- Media Queries ----- */
@media screen and (max-width: 600px) {
  .info-container {
    width: 90vw;
  }

  .quote-pad {
    width: 90vw;
  }

  .form-group {
    width: 80%;
  }
}

@media screen and (max-width: 450px) {
  .container {
    font-size: 19px;
  }
}

@media screen and (max-width: 300px) {
  .container {
    font-size: 14px;
  }

  .quote {
    border-top: 2px solid #87a7d3;
  }
}